.cart-item {
  margin: 0;
  padding: 0.75rem 0rem;
  border-bottom: 1px solid darken(white, 25%);
  align-items: center;

  p {
    margin: 0;
    font-size: 0.8rem;
    &.price {
      font-weight: 700;
      font-size: 1rem;
    }
    &.unit-price {
      align-self: flex-end;
      color: #b12704;
    }
  }

  .item-image {
    width: 7.5rem;
    height: 10rem;
  }

  .item-details {
    gap: 0.5rem;

    .item-name {
      font-weight: 500;
      font-size: 1.5rem;
      margin: 0;
    }
    button {
      &.allow {
        font-weight: 600;
        padding: 0.5rem 1.5rem;
      }
    }
  }
}
