.create-review{
    width: 90%;
    align-self: center;
    textArea{
        resize: none;
        padding-top: 0.5rem;
        padding-left: 0.3rem;
        border: 1px solid #D3D3D3;
        border-radius: 4px;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        border-bottom: 0;
        outline: 0;
        
    }
    button{
        padding: 0.4rem;
        border-radius: 4px;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        border:0;
    }
    p {
        margin-top: 0.4rem;
        font-size: 0.7rem;
        color: red;
    }
}